import React, { Component } from "react";
import {
  Card,
  CardImg,
  CardText,
  CardBody,
  CardTitle,
  CardSubtitle,
  Button
} from "reactstrap";
import { FaRegTrashAlt, FaRegEdit } from "react-icons/fa";

class ListItem extends Component {
  constructor(props) {
    super(props);
  }
  onHandleClickToEdit=()=>{
    alert("Edit")
  }
  onHandleClickToDel=()=>{
    alert("Delete")
  }
  render() {
    const { id, data_field, id_users } = this.props.list_massage;
    return (
      <div style={{ textAlign: "right" }}>
        <Card className="blockpost">
          <div>
            <a name={id} onClick={()=>this.onHandleClickToEdit()}>
              <FaRegTrashAlt />
            </a>
            <a name={id} onClick={()=>this.onHandleClickToDel()}>
              <FaRegEdit />
            </a>
          </div>
          <CardBody>
            <CardTitle>{id_users}</CardTitle>
            <CardText>
              <span>{data_field}</span>
              <span>{}</span>
            </CardText>
          </CardBody>
        </Card>
      </div>
    );
  }
}

export default ListItem;
